    z-index: 1;
    top: 6px;
    left: 13px;
    transform: translate(-50%, -50%);
    background-image: url(../images/check.svg);
  }
}

input[type="checkbox"] {
  display: none;

  &:checked ~ .checkbox {
    &::after {
      display: block;
    }
  }
}

